export default {
	data() {
		return {
			yesOrNoName: null,
			misId: '',
			info: '',
			upInfo: '',
			jpuzNum: 0,
			user_token: '',
			numList: [{
				name: '已申请'
			}, {
				name: '完成中'
			}, {
				name: '已完成'
			}, ]
		}
	},
	onLoad(e) {
		let that = this;
		that.misId = e.misId;
		console.log(e.misId);
		that.user_token = uni.getStorageSync("user_token");
		var info = uni.getStorageSync("square_info");
		that.info = info;
		uni.request({
			url: that.MisUrl + '/mis/getMisById/' + that.misId,
			method: 'POST',
			header: {
				'user_token': that.user_token
			},
			data: {},
			success: res => {},
			fail: () => {},
			complete: () => {}
		});
		uni.request({
			url: that.MisUrl + '/mis/getJpUzNumByJpUzId/' + that.info.jpuzId,
			method: 'POST',
			header: {
				'user_token': that.user_token
			},
			data: {},
			success: res => {
				var data = res.data.result;
				that.jpuzNum = data;
				console.log(that.jpuzNum);
			},
			fail: () => {},
			complete: () => {}
		});

	},
	methods: {
		NumSteps() {
			this.num = this.num == this.numList.length - 1 ? 0 : this.num + 1
		},
		showModal(e) {
			this.yesOrNoName = e.currentTarget.dataset.target
		},

		hide() {
			this.yesOrNoName = null,
				uni.showToast({
					title: '已取消',
					icon: 'none'
				});
		},
		//发起申请任务
		yes() {
			let that = this;
			that.yesOrNoName = null;
			uni.request({
				url: that.MisUrl + '/jpuz/userPutMis',
				method: 'GET',
				data: {},
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
